import { tsvParse } from  "d3-dsv";
import { timeParse } from "d3-time-format";
import 'url-search-params-polyfill';

function parseData(parse) {

	return function(d) {

		d.date = parse(d.date);
		d.open = +d.open;
		d.high = +d.high;
		d.low = +d.low;
		d.close = +d.close;
		d.volume = +d.volume;

		return d;
	};
}
